.dialogs {
    background: linear-gradient(#f3eaff, #f3eaff, #f8d8ff);
    height: 100%;
    display: grid;
    grid-template-columns: 2fr 8fr;
}

.dialogsItems {
    padding: 15px;
}

.dialogsItems .dialog {
    color: #004148;
}

.dialogsItems .active {
    color: rgba(0, 65, 72, 0.5);
}

.messages {
    padding: 15px;
}

.messages .message {
    color: #004148;
}

.dialog, .message {
    font-size: 17px;
    padding: 2px 3px;
}

.field {
    margin: 15px 0 10px 0;
    padding: 5px;
    border-radius: 10px;
    border: 3px solid #eda5ff;
    font-size: 15px;
}

.sendButton {
    height: 30px;
    border-radius: 10px;
    font-weight: bold;
    font-size: 15px;
    text-shadow: 0 0 10px rgb(254, 250, 255);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.38);
    border: none;
    background-color: rgb(243, 210, 255);
    color: #000000;
    padding: 0 10px;
    margin-bottom: 15px;
}

.sendButton:hover {
    border-radius: 9px;
    height: 32px;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.55);
}